import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
Vue.config.productionTip = false
// markdown
import 'github-markdown-css'
// devibeans   swh
import './assets/css/swh.css'

// md
import  { mdArticle } from './article/index'
Vue.use(mdArticle)

// 
import SupTopUI from './components/component_module'
Vue.use(SupTopUI);

//
import MyConfirm from './components/component_module/confirm//confirm.js'
Vue.prototype.$confirm = MyConfirm;


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
